const initEventTab = () => {
    const tabSection = document.querySelector('.event-tab');
    if (!tabSection) return;

    const buttons = tabSection.querySelectorAll('[data-tab]');
    const items = tabSection.querySelectorAll('.event-tab__item');

    buttons.forEach(button => {
        button.addEventListener('click', () => {
            const targetSelector = button.getAttribute('data-tab');
            const targetItem = tabSection.querySelector(targetSelector);

            if (!targetItem) return;

            // Убираем активные классы
            buttons.forEach(btn => {
                btn.classList.remove('event-tab__btn--active');
            });

            items.forEach(item => {
                item.classList.remove('event-tab__item--active');
            });

            // Добавляем активные классы
            button.classList.add('event-tab__btn--active');
            targetItem.classList.add('event-tab__item--active');
        });
    });

}

export{initEventTab};